// INPUT (Foundation default styles are unconvenient)
#{$all-text-inputs} {
  height: $form-height;
  padding: $form-padding;
  font-size: $input-font-size;
  font-family: $font-family-sans-serif;
  font-weight: $font-weight-medium;
  border: $input-border-width $input-border-style $input-border-color;

  @include placeholder {
    color: $grey;
    font-weight: $font-weight-normal;
  }

  &.small-padding {
    padding-left: rem-calc(5);
    padding-right: rem-calc(5);
  }

  &:focus {
    @include focus();
  }
}

.field {
  &__label {
    color: $blue;
    font-weight: $font-weight-bold;
    padding: rem-calc(17) 0;
  }

  input.field__input {
    border: 2px solid $input-border-color;
  }
}

.error {
  .error,
  .field__input {
    color: $alert-color !important;
  }
  small.error {
    text-transform: none !important;
  }
}


// SELECT-LIKE DROPDOWN
.form-dropdown {
  background: $input-bg-color;
  border: $input-border-width $input-border-style $input-border-color;
  border-radius: $input-border-radius;
  color: $input-font-color;
  margin-bottom: $form-spacing;
  min-height: $form-height;
  position: relative;

  &__trigger {
    @extend .btn-unstyled;
    position: relative;
    width: 100%;
    height: $form-height;
    padding: $form-padding rem-calc(60) $form-padding $form-padding;
    font-weight: $form-label-font-weight;
    font-size: $input-font-size;

    &:focus {
      @include focus();
    }

    .icon-expand {
      @include position(absolute, 50% 0 null null);
      @include transform(translateY(-50%));
      @include transition-property(all);
      @include transition-duration(.4s);
      @include transition-timing-function(ease-out);
      color: $blue;
      font-size: ($input-font-size * 1.35);
      margin-right: $form-padding;
    }

    &[aria-expanded="true"] {
      .icon-expand {
        @include transform(rotate(180deg) translateY(50%));
      }
    }
  }

  &__content {
    padding: $form-padding $form-padding 0 $form-padding;
    background: $input-bg-color;

    .undo-padding {
      margin-top: (-$form-padding);
    }

    position: absolute;
    z-index: 1;
    background: $input-bg-color;
    border: $input-border-width $input-border-style $input-border-color;
    width: rem-calc(250);
    box-shadow: rem-calc(3) rem-calc(3) rem-calc(3) 0 rgba($black, .2);

    .bordered {
      position: relative;

      &:after {
        content: "";
        //@include position(absolute, null (-$form-padding) 0 (-$form-padding));
        @include position(absolute, null 0 0 0);
        height: 1px;
        background-color: $input-border-color;
      }
    }

    .label-checkbox {
      display: block;
    }
  }
}


// CHECKBOX
.input-checkbox {
  @extend .visually-hidden;

  &:checked + label:before {
    background: $blue;
    box-shadow: inset 0 0 0 2px $white;
  }
  &:focus + label:before {
    @include focus();
    box-shadow: 0 0 4px 0 $glowing-effect-color,
                inset 0 0 0 2px $white;
  }
}

.label-checkbox {
  display: table;
  line-height: 1;
  padding: rem-calc(15) 0;
  @media #{$medium-up} {
    padding: rem-calc(15) 0;
  }
  cursor: pointer;

  &:before {
    content: "";
    width: rem-calc(30);
    height: rem-calc(30);
    border: 1px solid $blue;
    background: $white;
    display: table-cell;
    vertical-align: middle;

    @media #{$medium-up} {
      width: rem-calc(15);
      height: rem-calc(15);
    }
  }

  span {
    display: table-cell;
    vertical-align: middle;
    padding-left: $form-padding;
  }
}

input[type="checkbox"] + .label-checkbox {
  margin-right: 0;
  margin-left: 0;
}


// "?" picto
.infotip {
  position: relative;
  span { @extend .visually-hidden; }

  &:after {
    content: "?";
    cursor: pointer;
    display: inline-block;
    width: rem-calc(20);
    height: rem-calc(20);
    border-radius: 50%;
    background: $blue;
    color: $white;
    @include position(absolute, 0 0 0 0);
    line-height: 1.3;
    text-align: center;
    font-size: rem-calc(15);
  }
}
_:-ms-lang(x), .infotip:after { line-height: 1.4; } // IE10 and IE11 hack


// TAG
.tag {
  display: inline-block;
  background-color: $pink;
  padding: rem-calc(5) rem-calc(10);
  color: $white;
  font-size: rem-calc(13);
  font-weight: $font-weight-bold;
  border-radius: 4px;
}


// Button dropdown
[data-dropdown] {
  .icon-expand {
    @include transition-property(all);
    @include transition-duration(.4s);
    @include transition-timing-function(ease-out);
    &:after {
      @include position(absolute, 50% 0 null null);
    }
  }

  &[aria-expanded="true"] {
    .icon-expand {
      @include transform(rotate(180deg));
    }
  }
}

.f-dropdown {
  padding: rem-calc(10);
  opacity: 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .5);
  @include transform(translateY(-10px));
  @include transition-property(opacity, transform);
  @include transition-duration(.3s);
  @include transition-timing-function(ease-out);

  &.f-open-dropdown {
    opacity: 1;
    @include transform(translateY(0));
    outline: none;
  }
}


// ajax wait
.ajax-wait {
  position: relative;
  &:after {
    content: '';
    @include position(absolute, 0 0 0 0);
    background: rgba(0,0,0,.5);
  }
}


// burger button
.menu-btn {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: $topbar-height;
  height: $topbar-height;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
  background: $white;

  &:focus {
    outline: none;
  }

  span {
    display: block;
    @include position(absolute, 50% rem-calc(20) null rem-calc(20));
    @include transform(translateY(rem-calc(-3)));
    height: rem-calc(3);
    background: $black;
    transition: background 0s 0.2s;

    &:before,
    &:after {
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: rem-calc(3);
      background-color: $black;
      content: "";
      transition-duration: 0.2s, 0.2s;
      transition-delay: 0.2s, 0s;
    }

    &:before {
      top: -10px;
      transition-property: top, transform;
    }

    &:after {
      bottom: -10px;
      transition-property: bottom, transform;
    }
  }

  &.is-active span {
    background-color: $white;

    &:before {
      top: 0;
      transform: rotate(45deg);
    }

    &:after {
      bottom: 0;
      transform: rotate(-45deg);
    }

    &:before,
    &:after {
      transition-delay: 0s, 0.3s;
    }
  }
}