@media #{$medium-only} {
  body {
    overflow-x: hidden;
  }

  @media #{$portrait} {
    html, body {
      font-size: 80%
    }
  }
}

@media #{$small-only} {
  body {
    overflow-x: hidden;
  }
}


// WRAPPERS
// main site wrapper
.main-wrapper {
  width: 100%;
  margin: 0 auto;

  @media #{$medium-up} {
    max-width: $xlarge-breakpoint;
    min-width: em-calc(900);
    padding-left: 0;
    padding-right: 0;
  }
}

// large content wrapper
.wrapper {
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
  margin: 0 auto;

  @media #{$medium-only} {
    padding-left: 1%;
    padding-right: 1%;
  }

  @media #{$medium-up} {
    padding-left: $column-gutter;
    padding-right: $column-gutter;
  }

  @media #{$large-up} {
    max-width: em-calc(1220);
  }
}

// section spacing
.section-padding {
  margin-top: rem-calc(40);
  margin-bottom: rem-calc(40);
}

// prevent padding top on body (really bad)
.f-topbar-fixed {
  padding-top: 0 !important;

  #container {
    padding-top: $topbar-height;
  }
}


// small headers
.narrow-header {
  height: rem-calc(400);
  padding-top: rem-calc(95);
}