// BUTTONS
@mixin btn-style($color) {
  @include button(
    $bg: $color
  );
  padding: $button-med $button-lrg;
  border: 1px solid $color;

  .no-touch &:hover,
  .touch &:active,
  &:focus {
    background-color: $white;
    color: $color;
    border: 1px solid $color;
  }

  &:focus {
    @include focus();
  }

  @media #{$medium-up} {
    padding: $button-lrg ($button-lrg * 2);
  }

  .icon-arrow,
  .icon-expand {
    margin-left: rem-calc(10);
    vertical-align: inherit;
    &:before {
      display: inline-block;
      vertical-align: middle;
    }
  }
}

.btn-primary {
  @include btn-style($blue);
}

.btn-secondary {
  @include btn-style($jet);
}


// FILTERS
.active {
  .btn-filter {
    border-color: $blue;
    color: $blue;
  }
}
.btn-filter {
  background: $white;
  font-size: $button-font-med;
  font-weight: $font-weight-bold;
  border: solid 3px $lightBlue;
  color: $lightBlue;
  padding: $button-sml ($button-lrg * 2);
  display: inline-block;
  transition-property: border-color, color;
  transition-duration: 300ms;
  transition-timing-function: ease-out;

  .no-touch &:hover,
  &:active {
    border-color: $blue;
    color: $blue;
  }

  &:focus {
    @include focus();
  }

  @media #{$medium-up} {
    font-size: $button-font-lrg;
    padding-top: $button-med;
    padding-bottom: $button-med;
  }
}


// BUTTON WITH ICON
.btn-icon {
  position: relative;
  .button__content {
    display: inline-block;
    position: relative;
    padding-left: rem-calc(35);
  }

  .icon {
    vertical-align: middle;
    @include position(absolute, 50% null null 0);
    @include transform(translateY(-50%));
    font-size: 150%;
  }
}


// ROUNDED BUTTONS
.btn-rounded {
  border-radius: 4px;
  padding: ($button-lrg *2);
  font-size: rem-calc(24*2/3);

  @media #{$medium-up} {
    padding-top: ($button-med *2);
    padding-bottom: ($button-med *2);
    font-size: rem-calc(13);
  }
}


// UNSTYLE BUTTON
.btn-unstyled {
  cursor: pointer;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  background: transparent;
  padding: inherit;
  text-align: inherit;
  border: none;
  display: inline-block;
  @include appearance(none);
  font-family: $font-family-sans-serif;

  .no-touch &:hover,
  &:active {
    background: inherit;
    color: inherit;
  }

  &:focus {
    @include focus();
  }
}


// btn wrapper
.btn-wrapper {
  text-align: center;

  &.btn-wrapper--center {
    @media #{$medium-up} {
      text-align: center;
    }

    > * {
      @media #{$small-only} {
        width: 100%;
      }
    }
  }
}